let temp = 1
export default {
	data() {
		return {
			customTheme: '',
			focus: false,
			searchContent: '',
			searchResult: [],
			page: 1
		}
	},
	onLoad(e) {
		this.customTheme = this.$store.state.customTheme;
		this.searchContent = e.e
		this.searchNow()
	},
	onReady() {

	},
	onReachBottom() {
		if (temp) {
			temp = 0
			this.searchNow()
			setTimeout(() => {
				temp = 1
			}, 1000);
		}
	},
	methods: {
		searchNow() {
			this.$http({
				url: '/item/search/' + this.page,
				data: {
					keyword: this.searchContent
				}
			}).then(res => {
				for (let i of res) {
					this.searchResult.push(i)
				}
				this.page += 1
			})
		},
		blur() {
			this.focus = false
		},
		itemJump(id) {
			uni.navigateTo({
				url: `../home/item?id=${id}`,
				animationType: 'pop-in',
				animationDuration: 200
			});
		}
	}
}
